<template>
  <div class="hello-world">
    <el-breadcrumb style="margin-top:20px;">
      <el-breadcrumb-item :to="{ path: '/' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div class="user">
      <div class="user-icon">
        <img :src="require('../assets/' + userIcon)" alt="" />
      </div>
      <div class="user-name">{{ userName }}</div>
    </div>
    <div class="actions">
      <div class="action">
        <el-button
          type="primary"
          @click="
            loginForm.email = user.email;
            showLoginDialog = true;
          "
          v-if="!user.logined || user.loginType == 0"
          >{{ loginText }}</el-button
        >
      </div>
      <div class="action">
        <el-button
          type="info"
          @click="showSetEmailDialog = true"
          v-if="user.logined && user.loginType == 0 && !user.email"
          ></el-button
        >
      </div>
      <div class="action">
        <el-button
          type="text"
          @click="loginAsAnonymous"
          v-if="!user.logined"
          :loading="loginLoading"
          ></el-button
        >
      </div>
      <div class="action">
        <el-button
          type="success"
          v-if="!user.logined"
          @click="showRegisterDialog = true"
          ></el-button
        >
      </div>
      <div class="action">
        <el-popconfirm
          title=""
          @confirm="logout"
          v-if="user.logined && user.loginType != 0"
        >
          <el-button type="danger" :loading="logoutLoading" slot="reference"
            ></el-button
          >
        </el-popconfirm>
      </div>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="showLoginDialog" width="350px">
      <el-form
        :model="loginForm"
        :rules="loginRules"
        ref="loginForm"
        label-width="60px"
      >
        <el-form-item label="" prop="email">
          <el-input
            placeholder=""
            prefix-icon="el-icon-user"
            v-model.lazy.trim="loginForm.email"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            placeholder=""
            prefix-icon="el-icon-key"
            v-model.lazy.trim="loginForm.password"
            show-password
          >
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="login" :loading="loginLoading">
            
          </el-button>
          <el-button type="text" @click="forgetPassword"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showSetEmailDialog"
      width="350px"
    >
      <el-form
        :model="setEmailForm"
        :rules="setEmailRules"
        ref="setEmailForm"
        label-width="60px"
      >
        <el-form-item label="" prop="email">
          <el-input
            placeholder=""
            prefix-icon="el-icon-user"
            v-model.lazy.trim="setEmailForm.email"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            placeholder=""
            prefix-icon="el-icon-key"
            v-model.lazy.trim="setEmailForm.password"
            show-password
          >
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            v-on:click="setEmail"
            :loading="setEmailLoading"
          >
            
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="showRegisterDialog" width="350px">
      <el-form
        :model="registerForm"
        :rules="registerRules"
        ref="registerForm"
        label-width="60px"
      >
        <el-form-item label="" prop="email">
          <el-input
            placeholder=""
            prefix-icon="el-icon-user"
            v-model.lazy.trim="registerForm.email"
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            placeholder=""
            prefix-icon="el-icon-key"
            v-model.lazy.trim="registerForm.password"
            show-password
          >
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="success"
            v-on:click="register"
            :loading="registerLoading"
          >
            
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="showResetPasswordDialog"
      width="350px"
    >
      <el-form
        :model="resetPasswordForm"
        :rules="resetPasswordRules"
        ref="resetPasswordForm"
        label-width="60px"
      >
        <el-form-item label="" prop=" ">
          <el-input
            placeholder=""
            prefix-icon="el-icon-user"
            v-model.lazy.trim="resetPasswordForm.email"
          >
          </el-input>
        </el-form-item>
        <el-form-item>
          <el-button
            type="primary"
            v-on:click="resetPassword"
            :loading="resetPasswordLoading"
          >
            
          </el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: "EoCloud",
  props: {
    msg: String,
  },
  data() {
    return {
      user: {
        email: "",
        logined: false, // false-true-
        loginType: "", // 0-1-
      },
      showLoginDialog: false,
      // 
      loginForm: {
        email: "",
        password: "",
      },
      loginRules: {
        email: [{ required: true, trigger: "blur" }],
        password: [{ required: true, trigger: "blur" }],
      },
      loginLoading: false,
      // 
      logoutLoading: false,
      // 
      showSetEmailDialog: false,
      setEmailForm: {
        email: "",
        password: "",
      },
      setEmailRules: {
        email: [{ required: true, trigger: "blur" }],
        password: [{ required: true, trigger: "blur" }],
      },
      setEmailLoading: false,
      // 
      showRegisterDialog: false,
      registerForm: {
        email: "",
        password: "",
      },
      registerRules: {
        email: [{ required: true, trigger: "blur" }],
        password: [{ required: true, trigger: "blur" }],
      },
      registerLoading: false,
      // 
      showResetPasswordDialog: false,
      resetPasswordForm: {
        email: "",
      },
      resetPasswordRules: {
        email: [{ required: true, trigger: "blur" }],
      },
      resetPasswordLoading: false,
    };
  },
  computed: {
    // 
    userIcon() {
      if (this.user.logined) {
        return this.user.loginType == "0" ? "an-login.svg" : "logined.svg";
      }
      return "un-login.svg";
    },
    // 
    userName() {
      if (this.user.logined) {
        return this.user.loginType == "0" ? "" : this.user.email;
      }
      return "";
    },
    // 
    loginText() {
      return (this.user.email ? "" + this.user.email : "") + "";
    },
  },
  created() {
    // 
    this.cloudAuth.onLoginStateChanged((loginState) => {
      console.log(loginState);
      if (loginState) {
        // 
        this.user.logined = true;
        this.user.loginType = loginState.loginType == "ANONYMOUS" ? "0" : "1";
        this.user.email = loginState.user.email;
      } else {
        // 
        this.user.logined = false;
      }
    });
  },
  methods: {
    /**
     * 
     */
    async login() {
      this.loginLoading = true;
      try {
        await this.$refs.loginForm.validate();
        await this.cloudAuth.signInWithEmailAndPassword(
          this.loginForm.email,
          this.loginForm.password
        );
        // 
        this.showLoginDialog = false;
        this.$notify({
          message: "",
          type: "success",
        });
      } catch (error) {
        this.$notify({
          message: error.message || "",
          type: "error",
        });
      } finally {
        this.loginLoading = false;
      }
    },
    /**
     * 
     */
    async logout() {
      this.logoutLoading = true;
      try {
        this.cloudAuth.signOut();
      } catch (error) {
        this.$notify({
          message: error.message,
          type: "error",
        });
      } finally {
        this.logoutLoading = false;
      }
    },
    /**
     * 
     */
    async loginAsAnonymous() {
      this.loginLoading = true;
      try {
        await this.cloudAuth.anonymousAuthProvider().signIn();
        // 
        this.showLoginDialog = false;
      } catch (error) {
        this.$notify({
          message: error.message,
          type: "error",
        });
      } finally {
        this.loginLoading = false;
      }
    },
    /**
     * 
     */
    async setEmail() {
      this.setEmailLoading = true;
      try {
        // sdk bug
        await this.cloudAuth.currentUser.updateUsername(this.cloudAuth.currentUser.uid);
        // 
        await this.cloudAuth.currentUser.updatePassword(this.setEmailForm.password);
        // 
        await this.cloudAuth.currentUser.updateEmail(this.setEmailForm.email);
        // 
        this.showSetEmailDialog = false;
        this.$notify({
          message: "",
          type: "success",
        });
      } catch (error) {
        this.$notify({
          message: error.message,
          type: "error",
        });
      } finally {
        this.setEmailLoading = false;
      }
    },
    /**
     * 
     */
    async register() {
      this.registerLoading = true;
      try {
        // 
        await this.cloudAuth.signUpWithEmailAndPassword(
          this.registerForm.email,
          this.registerForm.password
        );
        // 
        this.showRegisterDialog = false;
        this.$notify({
          message: "",
          type: "success",
        });
      } catch (error) {
        this.$notify({
          message: error.message,
          type: "error",
        });
      } finally {
        this.registerLoading = false;
      }
    },
    /**
     * 
     */
    forgetPassword() {
      this.resetPasswordForm.email = this.loginForm.email;
      this.showResetPasswordDialog = true;
    },
    /**
     * 
     */
    async resetPassword() {
      this.resetPasswordLoading = true;
      try {
        // 
        await this.cloudAuth.sendPasswordResetEmail(this.resetPasswordForm.email);
        // 
        this.showResetPasswordDialog = false;
        this.$notify({
          message: "",
          type: "success",
        });
      } catch (error) {
        this.$notify({
          message: error.message,
          type: "error",
        });
      } finally {
        this.resetPasswordLoading = false;
      }
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.user {
  text-align: center;
  padding: 20px 0;
}
.actions {
  text-align: center;
}
.action {
  margin: 20px 0;
}
</style>
